<script>
  import AppHeaderHome from '../components/AppHeaderHome.vue';
  import BaseContainer from '../components/ui/BaseContainer.vue';
  import BaseText from '../components/ui/BaseText.vue';
  import BaseButton from '../components/ui/BaseButton.vue';
  import AppLoader from '../components/AppLoader.vue';
  import AppFooter from '../components/AppFooter.vue';
  import UserReview from '../components/UserReview.vue';
  import ProductsInModal from '../components/ProductsInModal.vue';
  import BaseModal from '../components/ui/BaseModal.vue';
  import { useHead } from 'unhead';

  import useVuelidate from '@vuelidate/core';
  import {
    required,
    email,
    numeric,
    minLength,
    maxLength,
  } from '@vuelidate/validators';
  import { validName } from '../Helper/Helper.js';

  export default {
    name: 'HomeView',
    components: {
      AppHeaderHome,
      AppLoader,
      BaseContainer,
      BaseText,
      BaseButton,
      UserReview,
      AppFooter,
      BaseModal,
      ProductsInModal,
    },
    data() {
      return {
        products: null,
        loader: false,
        form: {
          name: '',
          email: '',
          phone: null,
          productsCode: '',
          quantity: null,
        },
        slickSettings: {
          arrows: true,
          dots: false,
          slidesToShow: 2,
          slidesToScroll: 1,
          responsive: [
            {
              breakpoint: 600,
              settings: {
                slidesToShow: 1,
                slidesToScroll: 1,
                initialSlide: 2,
              },
            },
          ],
        },
        modalChoose: false,
        modalTeam: false,
        teamMembers: [
          {
            img: 'ProfilePicAkansha.jpg',
            name: 'Akansha Loya',
            position: 'Founder',
            linkedin: '#',
          },
          {
            img: 'ProfilePicAkshay.jpg',
            name: 'Akshay Loya',
            position: 'Co-founder',
            linkedin: '#',
          },
        ],
      };
    },

    validations() {
      return {
        form: {
          email: {
            required,
            email,
          },
          name: {
            required,
            name_validation: {
              $validator: validName,
              $message:
                'Invalid Name. Valid name only contain letters, dashes (-) and spaces',
            },
          },
          phone: {
            required,
            numeric,
            min: minLength(10),
            max: maxLength(10),
          },
        },
      };
    },
    setup() {
      return { v$: useVuelidate() };
    },
    created() {
      this.getProducts();
    },
    mounted() {
      useHead({
        title: 'B2B Gifting Solutions',
        meta: [
          {
            name: 'title',
            content: 'Order Personalized Gifts Online | The Rare Window ',
          },
          {
            name: 'description',
            content:
              'Unique and affordable B2B gifting solutions with a personal touch. Explore the best personalized corporate gifts online.',
          },
        ],
      });
      window.scrollTo(0, 0);
      this.deleteCookies();
    },
    methods: {
      deleteCookies() {
        if (
          document.cookie.includes('csrftoken') ||
          document.cookie.includes('sessionid')
        ) {
          const date = new Date(
            new Date().setTime(new Date().getUTCSeconds() + 1),
          );
          document.cookie = `csrftoken=;expires=${date};`;
          document.cookie = `sessionid=;expires=${date};`;
        }
      },
      async getProducts() {
        this.deleteCookies();
        await fetch('https://therarewindow.com/api/getB2BProduct/').then(
          (res) => {
            if (res.ok) {
              res.json().then((res) => (this.products = res.data));
            }
          },
        );
      },
      async submitReq() {
        this.deleteCookies();
        const result = await this.v$.$validate();
        if (!result) {
          return;
        } else {
          this.loader = true;
          var myHeaders = new Headers();
          myHeaders.append('Content-Type', 'application/json');

          let payload = JSON.stringify({
            name: this.form.name,
            email: this.form.email,
            phone_no: this.form.phone,
            product_code: this.form.productsCode,
            quantity: this.form.quantity,
          });
          const requestOptions = {
            method: 'POST',
            headers: myHeaders,
            body: payload,
          };
          await fetch(
            'https://therarewindow.com/api/b2bRequirementRequest/',
            requestOptions,
          )
            .then((res) => {
              if (res.ok) {
                res.json().then((data) => {
                  console.log(data);
                  this.v$.$reset();
                  this.form = {
                    name: '',
                    email: '',
                    phone: '',
                    product_code: '',
                    quantity: '',
                  };
                  this.$toast.open({
                    message:
                      'We appreciate you contacting us. One of our colleagues will get back in touch with you soon!Have a great day!',
                    type: 'success',
                    duration: 5000,
                    dismissible: true,
                    position: 'top',
                  });
                  this.loader = false;
                });
              } else {
                res.json().then((err) => console.log(err));
                this.$toast.open({
                  message: 'Request not submited due to some error.',
                  type: 'error',
                  duration: 5000,
                  dismissible: true,
                });
                this.loader = false;
              }
            })
            .catch((error) => console.log('error: ', error));
        }
      },
    },
  };
</script>
