          {
            name: 'description',
            content:
              'Give your loved ones a reason to smile with unique personalized gifts. Explore personalized gifts for couples, family & friends.',
          },
        ],
      });
      this.deleteCookies();
    },
    methods: {
      deleteCookies() {
        if (
          document.cookie.includes('csrftoken') ||
          document.cookie.includes('sessionid')
        ) {
          const date = new Date(
            new Date().setTime(new Date().getUTCSeconds() + 1),
          );
          document.cookie = `csrftoken=;expires=${date};`;
          document.cookie = `sessionid=;expires=${date};`;
        }
      },
      async submitContact() {
        this.deleteCookies();
        const result = await this.v$.$validate();
        if (!result) {
          return;
        } else {
          this.loader = true;
          var myHeaders = new Headers();
          myHeaders.append('Content-Type', 'application/json');

          let payload = JSON.stringify({
            name: this.form.name,
            email: this.form.email,
            phone_no: this.form.phone,
            message: this.form.msg,
          });
          const requestOptions = {
            method: 'POST',
            headers: myHeaders,
            body: payload,
          };
          await fetch(
            'https://therarewindow.com/api/contactUs/',
            requestOptions,
          )
            .then((res) => {
              if (res.ok) {
                res.json().then((data) => {
                  console.log(data);
                  this.v$.$reset();
                  this.form = {
                    name: '',
                    email: '',
                    phone: '',
                    msg: '',
                  };
                  this.$toast.open({
                    message:
                      'We appreciate you contacting us. One of our colleagues will get back in touch with you soon!Have a great day!',
                    type: 'success',
                    duration: 5000,
                    dismissible: true,
                    position: 'top',
                  });
                  this.loader = false;
                });
              } else {
                res.json().then((err) => console.log(err));
                this.$toast.open({
                  message: 'Details is not submitted due to some error.',
                  type: 'error',
                  duration: 5000,
                  dismissible: true,
                  position: 'top',
                });
                this.loader = false;
              }
            })
            .catch((error) => console.log('error: ', error));
        }
      },
    },
  };
</script>
