<script>
  import AppHeader from '@/components/AppHeader.vue';
  import AppLoader from '../components/AppLoader.vue';
  import BaseText from '@/components/ui/BaseText.vue';
  import BaseCard from '@/components/ui/BaseCard.vue';
  import BaseButton from '@/components/ui/BaseButton.vue';
  import useVuelidate from '@vuelidate/core';
  import {
    required,
    email,
    numeric,
    minLength,
    maxLength,
  } from '@vuelidate/validators';
  import { validName, validNonScriptText } from '../Helper/Helper.js';
  import { mapGetters, mapMutations } from 'vuex';

  export default {
    components: {
      AppHeader,
      AppLoader,
      BaseText,
      BaseCard,
      BaseButton,
    },
    data() {
      return {
        loader: false,
        countries: null,
        sameBillingAddress: true,
        couponCode: '',
        discount: 0,
        errorCouponCode: null,
        form: {
          email: '',
          send_news_and_Offers: false,
          shipping: {
            country: 'India',
            firstName: '',
            lastName: '',
            address: '',
            apartment: '',
            pin: '',
            city: '',
            state: '',
            phone: '',
          },
          billing: {
            country: 'India',
            firstName: '',
            lastName: '',
            address: '',
            apartment: '',
            pin: '',
            city: '',
            state: '',
            phone: '',
          },
        },
      };
    },
    validations() {
      return {
        form: {
          email: {
            required,
            email,
          },
          shipping: {
            country: {
              required,
            },
            firstName: {
              required,
              name_validation: {
                $validator: validName,
                $message:
                  'Invalid Name. Valid name only contain letters, dashes (-) and spaces',
              },
            },
            lastName: {
              required,
              name_validation: {
                $validator: validName,
                $message:
                  'Invalid Name. Valid name only contain letters, dashes (-) and spaces',
              },
            },
            phone: {
              required,
              numeric,
              min: minLength(10),
              max: maxLength(10),
            },
            address: {
              required,
              name_validation: {
                $validator: validNonScriptText,
                $message: 'Invalid text. please do not use "<script>" tag',
              },
            },
            apartment: {
              name_validation: {
                $validator: validNonScriptText,
                $message: 'Invalid text. please do not use "<script>" tag',
              },
            },
            pin: {
              numeric,
              required,
            },
            city: {
              required,
              name_validation: {
                $validator: validNonScriptText,
                $message: 'Invalid text. please do not use "<script>" tag',
              },
            },
            state: {
              required,
              name_validation: {
                $validator: validNonScriptText,
                $message: 'Invalid text. please do not use "<script>" tag',
              },
            },
          },
          billing: {
            country: {
              required,
            },
            firstName: {
              required,
              name_validation: {
                $validator: validName,
                $message:
                  'Invalid Name. Valid name only contain letters, dashes (-) and spaces',
              },
            },
            lastName: {
              required,
              name_validation: {
                $validator: validName,
                $message:
                  'Invalid Name. Valid name only contain letters, dashes (-) and spaces',
              },
            },
            phone: {
              required,
              numeric,
              min: minLength(10),
              max: maxLength(10),
            },
            address: {
              required,
              name_validation: {
                $validator: validNonScriptText,
                $message: 'Invalid text. please do not use "<script>" tag',
              },
            },
            apartment: {
              name_validation: {
                $validator: validNonScriptText,
                $message: 'Invalid text. please do not use "<script>" tag',
              },
            },
            pin: {
              numeric,
              required,
            },
            city: {
              required,
              name_validation: {
                $validator: validNonScriptText,
                $message: 'Invalid text. please do not use "<script>" tag',
              },
            },
            state: {
              required,
              name_validation: {
                $validator: validNonScriptText,
                $message: 'Invalid text. please do not use "<script>" tag',
              },
            },
          },
        },
      };
    },
    setup() {
      return { v$: useVuelidate() };
    },
    computed: {
      ...mapGetters('checkout', [
        'getPrice',
        'getProductDetails',
        'getImages',
        'getPreviewImg',
        'getNumOfMaps',
      ]),
    },
    created() {
      if (this.getProductDetails.productName == '') {
        this.$router.push('/');
      }
    },
    mounted() {
      this.getCountries();
      this.deleteCookies();
    },
    methods: {
      ...mapMutations('checkout', [
        'setShippingDetails',
        'setBillingDetails',
        'setUserEmail',
        'setSend_news_and_Offers',
        'setPrice',
        'setProductName',
      ]),
      deleteCookies() {
        if (
          document.cookie.includes('csrftoken') ||
          document.cookie.includes('sessionid')
        ) {
          const date = new Date(
            new Date().setTime(new Date().getUTCSeconds() + 1),
          );
          document.cookie = `csrftoken=;expires=${date};`;
          document.cookie = `sessionid=;expires=${date};`;
        }
      },
      async applyCoupon() {
        this.deleteCookies();
        let myHeaders = new Headers();
        myHeaders.append('Content-Type', 'application/json');
        let raw = JSON.stringify({
          coupon_code: this.couponCode,
          productName: this.getProductDetails.productName,
          frameSize: this.getProductDetails.frameSize,
          giftWrap: this.getProductDetails.giftWrap,
          printingType: this.getProductDetails.printingType,
          mapType:
            this.getProductDetails.productName == 'JourneyMap'
              ? 1
              : this.getNumOfMaps,
          frameColor: this.getProductDetails.frameColor,
        });
        let requestOptions = {
          method: 'POST',
          headers: myHeaders,
          body: raw,
          redirect: 'follow',
        };
        await fetch(
          'https://therarewindow.com/api/calculatePrice/',
          requestOptions,
        )
          .then((res) => res.json())
          .then((result) => {
            if (result.data) {
              this.setPrice(result.data.price);
              this.errorCouponCode = '';
              this.discount = result.data.discount;
            } else {
              this.errorCouponCode = result.messages;
            }
          })
          .catch((err) => console.log(err));
      },
      async getCountries() {
        this.deleteCookies();
        await fetch('/config/countries.json')
          .then((res) => res.json())
          .then((data) => (this.countries = data));
      },
      async continuePayment() {
        this.deleteCookies();
        if (this.sameBillingAddress) {
          this.makeSameBillingAddress();
        }
        const result = await this.v$.$validate();
        if (!result) {
          return;
        } else {
          this.loader = true;
          this.getProductDetails.mapType =
            this.getProductDetails.productName == 'Journey Map'
              ? 1
              : this.getProductDetails.mapType;
          let raw = JSON.stringify({
            send_news_and_Offers: this.form.send_news_and_Offers,
            total_amount: this.getPrice,
            discount_code: this.couponCode,
            quantity: 1,
            email: this.form.email,
            product: this.getProductDetails,
            final_image: this.getImages.final,
            base_image: this.getImages.base,
            map_image: this.getImages.map,
            shipping_address: this.form.shipping,
            billing_address: this.form.billing,
          });
          const myHeaders = new Headers();
          myHeaders.append('Content-Type', 'application/json');
          const requestOptions = {
            method: 'POST',
            headers: myHeaders,
            body: raw,
            redirect: 'follow',
          };
          await fetch(
            'https://therarewindow.com/api/createOrder/',
            requestOptions,
          ).then((res) => {
            if (res.ok) {
              res.json().then((data) => {
                if (data.status == 200) {
                  this.loader = false;
                  this.setProductName('');
                  this.$router.push(
                    '/proceed-to-payment?orderId=' + data.data.uuid,
                  );
                } else {
                  this.loader = false;
                  this.$toast.open({
                    message: data.messages,
                    type: 'error',
                    duration: 5000,
                    dismissible: true,
                    position: 'top',
                  });
                }
              });
            }
          });
        }
      },
      makeSameBillingAddress() {
        this.deleteCookies();
        if (this.sameBillingAddress) {
          this.form.billing = this.form.shipping;
        } else {
          this.form.billing = {
            firstName: '',
            lastName: '',
            address: '',
            apartment: '',
            pin: '',
            city: '',
            state: '',
            phone: '',
            country: 'India',
          };
        }
      },
    },
  };
</script>

<style></style>
