    text-align: center;
    padding: 0 16px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
  }
  .preview {
    position: relative;
    margin: auto;
    outline: 6px solid transparent;
  }
  .frame12x8 {
    aspect-ratio: 12 / 8;
  }
  .frame8x12 {
