              :key="index"
              :class="
                focusDetails[index - 1] == 'details' + [index - 1] &&
                (title[index - 1] == '' || subTitle[index - 1] == ''
                  ? 'errorInput rounded-lg'
                  : '')
              "
              open
            >
              <template #icon
                ><img src="@/assets/images/marker.svg"
              /></template>
              <template #title>Map {{ index }} Design</template>
              <template #body>
                <div class="px-5 mb-8">
                  <div class="mb-2 font-semibold">Title*</div>
                  <input
                    type="text"
                    class="form-control"
                    placeholder="Enter your title"
                    maxlength="25"
                    v-model="title[index - 1]"
                    @input="setTitle({ index: index, title: title[index - 1] })"
                  />
                </div>
                <div class="px-5 mb-8">
                  <div class="mb-2 font-semibold">Subtext*</div>
                  <input
                    type="text"
                    class="form-control"
                    placeholder="Enter your subtext"
                    maxlength="25"
                    v-model="subTitle[index - 1]"
                    @input="
                      setSubTitle({ index: index, sub: subTitle[index - 1] })
                    "
                  />
                </div>
                <div class="px-5 mb-8">
                  <div class="mb-2 font-semibold">Image</div>
                  <image-drag
                    class="w-full"
                    :id="`imageDrag${index - 1}`"
                    @load-image="updateImage($event, index)"
                  />
                </div>
              </template>
            </base-card>
            <!-- <div class="px-5 mb-8">
              <div class="mb-2 font-semibold">Left Footer</div>
              <input
                type="text"
                class="form-control"
                :class="
                  focus == 'leftFooter' && leftFooterText == ''
                    ? 'errorInput'
                    : ''
                "
                placeholder="Your left footer"
                maxlength="25"
                v-model="leftFooterText"
                @input="setLeftFooter(leftFooterText)"
              />
              <small class="text-xs text-ext-gray">Max 25 characters </small>
            </div> -->
            <!-- <div class="px-5 mb-8">
              <div class="mb-2 font-semibold">Main Footer</div>
              <input
                type="text"
                class="form-control"
                :class="
                  focus == 'mainFooter' && mainFooterText == ''
                    ? 'errorInput'
                    : ''
                "
                placeholder="Your footer"
                maxlength="25"
                v-model="mainFooterText"
                @input="setMainFooter(mainFooterText)"
              />
              <small class="text-xs text-ext-gray">Max 25 characters </small>
