<script>
  import AppHeader from '../components/AppHeader.vue';
  import AppLoader from '../components/AppLoader.vue';
  import JourneyMapPreview from '../components/JourneyMapPreview.vue';
  import ChooseBg from '../components/productOptions/ChooseBg.vue';
  import LocationInput from '../components/productOptions/LocationInput.vue';
  import MapStyling from '../components/productOptions/MapStyling.vue';
  import MapComponent from '@/components/map/MapComponent.vue';
  import BaseCard from '@/components/ui/BaseCard.vue';
  import FrameColor from '../components/productOptions/FrameColor.vue';
  import PrintingType from '../components/productOptions/PrintingType.vue';
  import { mapGetters, mapMutations, mapActions } from 'vuex';
  import ImageDrag from './ImageDrag.vue';
  export default {
    components: {
      AppHeader,
      AppLoader,
      JourneyMapPreview,
      LocationInput,
      ChooseBg,
      MapStyling,
      FrameColor,
      PrintingType,
      MapComponent,
      BaseCard,
      ImageDrag,
    },
    data() {
      return {
        selectedTab: 'preview',
        title: ['', '', '', '', ''],
        subTitle: ['', '', '', '', ''],
        markerLabel: ['', '', '', '', ''],
        focusDetails: ['', null, null],
        giftingMessage: '',
        giftWrapping: false,
        printType: null,
        price: null,
        alternativeFont: false,
        loader: false,
        locationAdded: [false, false, false, false, false],
        tabDesignValidated: false,
        tabDetailsValidated: false,
        focus: '',
        lastActiveTab: 'design',
        headerText: '',
        subHeader: '',
        leftFooterText: '',
        rightFooterText: '',
        mainFooterText: '',
      };
    },
    computed: {
      ...mapGetters('journeyMap', [
        'selectedFrameSize',
        'selectedOption',
        'getNumOfMap',
      ]),
      ...mapGetters('checkout', ['getPrice', 'getImages', 'getFinalImage']),
      ...mapGetters('mapObj', [
        'mapDataUrl',
        'getMapMarker',
        'getMapStyle',
        'getCenter',
        'getMarkerLabel',
      ]),
      designSelected() {
        return this.selectedTab == 'design';
      },
      detailsSelected() {
        return this.selectedTab == 'details';
      },
      formatSelected() {
        return this.selectedTab == 'format';
      },
      previewSelected() {
        return this.selectedTab == 'preview';
      },
      mobileView() {
        return window.screen.width < 1024;
      },
      ctaBtnText() {
        return this.designSelected || this.detailsSelected
          ? 'Next'
          : 'Checkout';
      },
    },
    created() {
      this.mobileView
        ? (this.selectedTab = 'preview')
        : (this.selectedTab = 'design');
      this.resetMap();
      this.resetDetails();
      this.deleteCookies();
    },
    mounted() {
      this.deleteCookies();
    },
    methods: {
      ...mapMutations('mapObj', [
        'setMapStyle',
        'setCenter',
        'resetMap',
        'setMarkerLabel',
      ]),
      ...mapMutations('journeyMap', [
        'setBg',
        'setFrameColor',
        'setPrintingType',
        'setTitle',
        'setSubTitle',
        'setDetailsHeaderText',
        'setDetailsSubHeaderText',
        'setImage',
        'setLeftFooter',
        'setMainFooter',
        'setRightFooter',
        'setGiftWrapping',
        'setAlternativeFont',
        'resetDetails',
      ]),
      ...mapMutations('checkout', ['setProductDetails', 'setNumOfMaps']),
      ...mapActions('checkout', ['checkPrice']),
      ...mapActions('journeyMap', ['createImg']),
      deleteCookies() {
        if (
          document.cookie.includes('csrftoken') ||
          document.cookie.includes('sessionid')
        ) {
          const date = new Date(
            new Date().setTime(new Date().getUTCSeconds() + 1),
          );
          document.cookie = `csrftoken=;expires=${date};`;
          document.cookie = `sessionid=;expires=${date};`;
        }
      },
      resizeMap(index) {
        this.deleteCookies();
        this.$refs.mapComponent[index].resizeMap();
      },
      updateMapStyle(style, index) {
        this.deleteCookies();
        this.setMapStyle({
          name: style.name,
          api: style.mapTileApi,
          key: index,
        });
      },
      updateLocation(center, key) {
        this.deleteCookies();
        this.setCenter({ center: center, key: key });
      },
      updatePrintType(type) {
        this.deleteCookies();
        this.setPrintingType(type);
        this.printType = type.name;
        if (this.printType == 'Digital Copy') {
          this.giftWrapping = false;
          this.setGiftWrapping(this.giftWrapping);
          this.checkPrice('journeyMap');
        } else {
          this.checkPrice('journeyMap');
        }
      },
      updateImage(e, index) {
        const payload = {
          index: index,
          image: e,
        };
        this.setImage(payload);
      },
      back() {
        this.deleteCookies();
        if (this.designSelected) {
          this.selectedTab = 'preview';
        }
        if (this.detailsSelected) {
          this.selectedTab = 'design';
        }
        if (this.formatSelected) {
          this.selectedTab = 'details';
        }
      },
      async next() {
        this.deleteCookies();
        if (this.designSelected) {
          let count = 0;
          for (let i = 0; i < this.getNumOfMap; i++) {
            if (!this.locationAdded[i]) {
              count++;
              this.$refs.locationInput[i].makeFocus();
              this.$toast.open({
                message: `Please select location ${i + 1}.`,
                type: 'error',
                duration: 5000,
                dismissible: true,
                position: 'top',
              });
              this.tabDesignValidated = false;
            }
          }
          if (count == 0) {
            this.selectedTab = 'details';
            this.$refs.options.scrollTop = 0;
            this.tabDesignValidated = true;
          }
        } else if (this.detailsSelected) {
          if (this.headerText && this.subHeader) {
            let count = 0;
            for (let i = 0; i < this.getNumOfMap; i++) {
              if (this.title[i] == '' || this.subTitle[i] == '') {
                count++;
                this.focusDetails[i] = 'details' + i;
                this.$toast.open({
                  message: `Please add map ${i + 1} design details.`,
                  type: 'error',
                  duration: 5000,
                  dismissible: true,
                  position: 'top',
                });
              }
            }
            if (count == 0) {
              this.selectedTab = 'format';
              this.$refs.options.scrollTop = 0;
              this.tabDetailsValidated = true;
            }
          } else {
            if (!this.headerText) {
              this.focus = 'header';
              this.$toast.open({
                message: `Please add header text.`,
                type: 'error',
                duration: 5000,
                dismissible: true,
                position: 'top',
              });
            }
            if (!this.subHeader) {
              this.focus = 'subHeader';
              this.$toast.open({
                message: `Please add subheader text.`,
                type: 'error',
                duration: 5000,
                dismissible: true,
                position: 'top',
              });
            }
          }
        } else if (this.formatSelected) {
          this.loader = true;
          this.selectedTab = 'preview';
          this.$refs.options.scrollTop = 0;
          this.checkout();
          setTimeout(async () => {
            await this.createImg();
          }, 4000);
        }
      },
      checkout() {
        this.deleteCookies();
        this.setProductDetails({
          mapType: 1,
          productName: this.selectedOption.productName,
          frameSize: '8x12', //this.selectedOption.frameSize,
          frameColor: this.selectedOption.frameColor.color,
          printingType: this.selectedOption.printingType,
          giftingMsg: this.giftingMessage,
          giftWrap: this.giftWrapping,
          blobImage: this.getImages,
        });
        this.setNumOfMaps(this.getNumOfMap);
      },
    },
    watch: {
      getFinalImage(newVal) {
        this.deleteCookies();
        if (newVal) {
          this.$router.push('/checkout');
        }
      },
      designSelected(newVal) {
        this.deleteCookies();
        if (newVal == true) {
          // this.$refs.mapComponent[0].resizeMap();
        }
      },
      formatSelected(newVal) {
        if (newVal) {
          this.deleteCookies();
          this.checkPrice('journeyMap');
        }
      },
      selectedTab(newVal) {
        this.deleteCookies();
        if (newVal != 'preview') {
          this.lastActiveTab = newVal;
        }
      },
    },
  };
</script>
<style scoped>
  .sideBar {
    height: calc(100vh - 65px);
  }
</style>
